.footer {
  text-align: center;
}

.inner {
  padding-top: 47px;
  border-top: 1px solid rgba(139, 139, 139, 0.2);
}

.link {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
  font-size: 24px;
  line-height: 1.3;
  color: #0b0b0b;

  svg {
    margin-bottom: 16px;
    width: 32px;
    height: 28px;
  }
}

.text {
  position: relative;
  bottom: -18px;
  font-weight: 900;
  font-size: 72px;
  line-height: 120%;
  color: #f1f0f0;
}